import qs from 'qs'
import $store from '@/store'

// 处理空属性
export function deleteEmptyObj(data) {
  for (const i in data) {
    if (data[i] === null || data[i] === undefined || data[i] === '' || data[i] === ' ') {
      delete data[i]
    }
  }
  return data
}

export function promiseWrapper(p) {
  return new Promise(resolve => {
    p.then((res = {}) => {
      const { code = 0 } = res
      resolve({
        data: res,
        success: code === 0
      })
    }).catch((e = {}) => {
      resolve({
        data: e,
        success: false
      })
    })
  })
}

export function requestWrapper(req) {
  return new Promise(resolve => {
    req.then((res = {}) => {
      resolve({
        ...res,
        success: res.success || res.code === 0
      })
    }).catch((e = {}) => {
      resolve({
        data: e,
        success: false
      })
    })
  })
}

export function historyReplaceState(query = {}) {
  window.history.replaceState(query, window.document.title, '?' + qs.stringify(query))
}

export function downloadFile(fileName, data) {
  const blob = new Blob([data])
  if ('download' in document.createElement('a')) { // 非IE下载
    const elink = document.createElement('a')
    elink.download = fileName
    elink.style.display = 'none'
    elink.href = URL.createObjectURL(blob)
    document.body.appendChild(elink)
    elink.click()
    URL.revokeObjectURL(elink.href) // 释放URL 对象
    // document.body.removeChild(elink)
  } else { // IE10+下载
    navigator.msSaveBlob(blob, fileName)
  }
}

export function isImage(ext = '') {
  const str = 'png,jpg,jpeg,bmp'
  return str.includes(ext.toLowerCase())
}
export function isVideo(ext = '') {
  const str = 'mp4,avi,flv'
  return str.includes(ext.toLowerCase())
}
export function isPdf(ext = '') {
  return ext.toLowerCase() === 'pdf'
}

export function code2text(value = '', enums = []) {
  // console.log(1, value, enums)
  return enums.find(item => item.value === value).label
}

// 删除标签
export function delTagsView(view = {}) {
  $store.dispatch('tagsView/delView', view)
}
