<template>
  <el-drawer
    :center="true"
    :append-to-body="true"
    :close-on-press-escape="false"
    :wrapper-closable="false"
    :title="title"
    size="50"
    :visible="value"
    :destroy-on-close="true"
    :before-close="beforeClose"
    @open="initSelect"
  >
    <el-form ref="form" :model="form" :rules="rules" label-width="120px">
      <el-scrollbar class="scroll-drawer">
        <el-form-item label="单据类型" prop="type">
          <el-checkbox-group v-model="form.type" @change="handleTypeChange">
            <el-checkbox v-for="(c, i) in orderTypes" :key="i" :label="c.value">{{ c.name }}</el-checkbox>
          </el-checkbox-group>
        </el-form-item>
        <template v-if="form.listForm.length">
          <div v-for="(c, i) in form.listForm" :key="i">
            <div class="title-type">{{ c.type | typeText(orderTypes) }}</div>
            <!-- :rules="{ required: true, message: '文档名称不能为空', trigger: 'blur'}" -->
            <el-form-item label="文档名称" :prop="`listForm[${i}].docName`">
              <el-input v-model="form.listForm[i].docName" clearable placeholder="请输入" style="width: 178px;" />
            </el-form-item>
            <el-form-item label="附件" :prop="`listForm[${i}].fileId`" :rules="{required: true, message: '附件不能为空', trigger: 'change'}">
              <el-upload
                ref="upload"
                sizi="mini"
                :action="`${BASE_API}/wms/file/upload`"
                :headers="headers"
                :file-list="form.listForm[i].fileList"
                :on-success="(response, file, fileList) => handleUploadSuccess(response, file, fileList, c)"
                :on-remove="(file, fileList) => handleRemove(file, fileList, c)"
                :on-preview="handlePreview"
                :before-remove="beforeRemove"
              >
                <el-button size="mini" icon="el-icon-upload" type="primary">点击上传</el-button>
              </el-upload>
            </el-form-item>
          </div>
        </template>
      </el-scrollbar>
      <div class="drawer-footer">
        <el-button :loading="loading" type="primary" @click="handleConfirm('form')">{{ loading ? '提交中 ...' : '确 定' }}</el-button>
        <el-button @click="beforeClose">取 消</el-button>
      </div>
    </el-form>

  </el-drawer>
</template>

<script>
import $store from '@/store'
import { addApi, editApi } from '@/apis/modules/in-storage/purchase/document'
import { deleteUploadedFileCommonApi } from '@/apis/modules/common'

const INIT_FORM = () => ({
  type: [], // 单据类型
  listForm: []
})

export default {
  components: {
  },
  filters: {
    typeText(type, list) {
      return list.find(c => c.value === type) ? list.find(c => c.value === type).name : ''
    }
  },
  mixins: [],
  props: {
    value: {
      type: Boolean,
      default: false
    },
    title: {
      type: String,
      default: ''
    },
    editType: {
      type: String,
      default: 'add'
    },
    dialogForm: {
      type: Object,
      default: () => {
        return { ...INIT_FORM() }
      }
    },
    extraInfo: {
      type: Object,
      default: () => ({})
    }
  },
  data() {
    const token = `${$store.getters.token}`
    return {
      form: { ...INIT_FORM() },
      loading: false,
      orderTypes: [{ name: '交接单', value: '1' }, { name: '验收单', value: '2' }, { name: '入库单', value: '3' }, { name: '其它', value: '4' }],
      rules: {
        type: [
          { type: 'array', required: true, message: '请至少选择一个单据类型', trigger: 'change' }
        ]
      },
      headers: { 'Authorization': token },
      BASE_API: process.env.VUE_APP_BASE_API_WMS
    }
  },
  computed: {
  },
  watch: {
    'form.type': {
      handler(type_list) {
        const types = this.form.listForm.map(c => c.type)
        const delInd = this.form.listForm.findIndex(c => !type_list.includes(c.type))
        // 取消勾选同步数据
        if (delInd !== -1) {
          this.form.listForm.splice(delInd, 1)
          return
        }
        // this.form.listForm没有所选的类型就追加数据
        type_list.forEach(c => {
          if (!types.includes(c)) {
            this.form.listForm
              .push({
                type: c,
                docName: '',
                fileId: '',
                fileName: '',
                fileList: []
              })
          }
        })
      }
    },
    'form.listForm': {
      handler() {
        this.$refs.form && this.$refs.form.validate()
      },
      deep: true
    }
  },
  mounted() {
    this.form.listForm = []
  },
  methods: {
    initSelect() {
    },
    // 确认操作
    handleConfirm(formName) {
      this.$refs[formName].validate((valid) => {
        const api = this.editType === 'add' ? addApi : editApi
        if (valid) {
          this.loading = false
          const params = this.form.listForm.map(c => ({ type: c.type, fileId: c.fileId, docName: c.docName || c.fileName, fileGroupId: this.extraInfo.fileGroupId }))
          api(params).then(res => {
            const { code } = res
            if (!code) {
              this.$message({
                message: `${this.title}成功`,
                type: 'success'
              })
              this.$emit('submit')
              this.beforeClose()
            }
            this.loading = false
          }).catch(err => {
            console.log(err)
            this.loading = false
          })
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
    // 文件上传成功
    handleUploadSuccess(response, file, fileList, { type }) {
      const { code, data, msg } = response
      if (code && code === 999) {
        this.$notify.error({
          title: '错误',
          message: msg || '文件上传失败'
        })
        return
      }
      if (!code) {
        this.form.listForm.forEach(c => {
          if (c.type === type) {
            c.fileId = data.id
            c.fileName = data.fileName
            c.fileList = [{ ...data, fileId: data.id, name: data.fileName, url: data.fileUrl }]
          }
        })
      }
    },
    // 点击文件列表中已上传的文件
    handlePreview(file) {
      const { downloadUrl } = file
      window.open(`${process.env.VUE_APP_BASE_API_WMS}${downloadUrl}`)
    },
    // 文件移除前
    beforeRemove(file, fileList) {
      return this.$confirm(`确定移除 ${file.name}？`, '提示', {
        confirmButtonText: '确 定',
        cancelButtonText: '取 消',
        cancelButtonClass: 'cancel-button-class',
        confirmButtonClass: 'confirm-button-class',
        type: 'warning',
        lockScroll: false
      })
    },
    // 文件移除操作
    handleRemove(file, fileList, { type }) {
      return new Promise((resolve, reject) => {
        deleteUploadedFileCommonApi({
          id: file.fileId
        }).then(res => {
          const { code } = res
          if (!code) {
            this.form.listForm.forEach(c => {
              if (c.type === type) {
                c.fileList = []
                c.fileId = ''
                c.fileName = ''
              }
            })
            resolve(res)
          } else {
            reject(res)
          }
        }).catch(err => {
          reject(err)
        })
      })
    },
    beforeClose(done) {
      this.$refs.form.resetFields()
      this.form = INIT_FORM()
      this.$emit('input', false)
      typeof done === 'function' && done()
    },
    // 单据类型改变
    handleTypeChange(val) {
      console.log('val----change: ', val)
    }
  }
}
</script>

<style lang='scss' scoped>
.container{
width: 860px;
margin: 0 auto;
.left{
    width: 640px;
}
.right{
    margin-left: 20px;
    width: 180px;
}
}
::v-deep:focus {
outline: 0;
}
.scroll-drawer {
height: calc(100vh - 150px);
// overflow: auto;
}
::v-deep .el-scrollbar .el-scrollbar__wrap {
overflow-x: hidden;
}
::v-deep .el-drawer {
width: 640px;
}
.drawer-footer{
text-align: right;
}

::v-deep .el-upload-list__item.is-success.focusing .el-icon-close-tip {
display: none !important;
}
.title-type {
  font-size: 16px;
  font-weight: bold;
  margin: 15px 25px 15px;
}
</style>

