<template>
  <div>
    <!-- 文件分组 -->
    <div class="wrapperBox">
      <div v-for="(c, i) in fileGroupList" :key="i" class="boxItem" :style="{backgroundColor: colors[i]}" :class="{checkedFileGroup: curGroupInfo.id === c.id}" @click="handleClickTag(c)">
        <div class="closeBtn" @click="handleDelTag(c)">
          <i class="closeIcon el-icon-close" />
        </div>
        <div class="boxItemLeft">
          <div>{{ i+1 }}</div>
          <div>{{ c.createDate }}</div>
        </div>
        <div class="boxItemRight">
          <span class="el-icon-notebook-1 pic" />
        </div>
      </div>
      <!-- 添加文件分组按钮 -->
      <div class="addBoxItemBtn" @click="handleAddTag">
        <i class="el-icon-plus" />
      </div>
    </div>
    <div class="tagContent">
      <el-row v-if="fileGroupList.length">
        <el-button type="primary" @click="handleAdd">添加</el-button>
      </el-row>
      <el-empty v-if="!tableData.length" class="empty-txt">
        <template slot="description">
          <el-button type="text" style="font-size: 23px;">
            暂无文档内容
          </el-button>
        </template>
      </el-empty>
      <div v-else class="box-list">
        <div v-for="item in tableData" :key="item.id" class="box-item">
          <el-descriptions class="box-item-left" :column="1" :colon="false" :title="item.type | formatterType">
            <el-descriptions-item label="上传时间：">{{ item.updateDate || '---' }}</el-descriptions-item>
            <el-descriptions-item label="上传人：">{{ item.uploaderName || '---' }}</el-descriptions-item>
            <el-descriptions-item label="文档：">
              <div style="color: #409eff;" @click="handleDownloadFile(item)">
                {{ item.docName || item.fileName }}.{{ item.suffix }}
              <!-- <div style="color: #409eff;" @click="handleDownloadFile(item)">{{ item.fileName || '---' }}</div> -->
              <!-- <span>.{{ item.suffix }}</span> -->
              </div>
            </el-descriptions-item>
          </el-descriptions>
          <div class="box-item-right">
            <el-button type="primary" @click="handleModify(item)">编辑</el-button>
            <el-button type="danger" @click="handleDelete(item)">删除</el-button>
          </div>
        </div>
      </div>
    </div>
    <!-- 编辑 -->
    <edit-dialog
      v-model="dialogVisible"
      :dialog-form="dialogForm"
      :edit-type="editType"
      :title="dialogTitle"
      :extra-info="{fileGroupId: curGroupInfo.id}"
      @submit="init"
    />
    <!-- 新增 -->
    <add-dialog
      v-model="dlgView"
      :dialog-form="dialogForm"
      :edit-type="editType"
      :title="dialogTitle"
      :extra-info="{fileGroupId: curGroupInfo.id}"
      @submit="init"
    />
  </div>
</template>

<script>
import paginationPage from '@/mixins/paginationPage'
import EditDialog from './EditDlg'
import AddDialog from './AddDlg'
import { getListApi, delApi, addFileGroupApi, deleteFileGroupApi, getFileGroupListApi } from '@/apis/modules/in-storage/purchase/document'
import axios from 'axios'
import { downloadFile } from '@/utils/utils'

export default {
  name: 'InStoragePurchaseDocument',
  components: {
    EditDialog,
    AddDialog
  },
  filters: {
    formatterType(type) {
      const obj = { 1: '交接单', 2: '验收单', 3: '入库单', 4: '其他' }
      return obj[type]
    }
  },
  mixins: [paginationPage],
  data() {
    return {
      title: this.$route.meta.title,
      tableData: [],
      dialogForm: {},
      editType: 'add',
      dialogVisible: false,
      dlgView: false,
      fileGroupList: [],
      curGroupInfo: {}, // 当前所点文件分组信息
      colors: [
        '#2ec7c9',
        '#b6a2de',
        '#5ab1ef',
        '#ffb980',
        '#d87a80',
        '#8d98b3',
        '#e5cf0d',
        '#97b552',
        '#95706d',
        '#dc69aa',
        '#07a2a4',
        '#9a7fd1',
        '#588dd5',
        '#f5994e',
        '#c05050',
        '#59678c',
        '#c9ab00',
        '#7eb00a',
        '#6f5553',
        '#c14089'
      ]
    }
  },
  computed: {
    dialogTitle() {
      if (this.editType === 'add') {
        return `新增${this.title}信息`
      } else {
        return `编辑${this.title}信息`
      }
    }
  },
  mounted() {
    this.getFileGroupList()
      .then(res => {
        const { data } = res
        if (data.length) {
          this.handleClickTag(data[0])
        }
      })
  },
  methods: {
    // 根据文档分组id查询文件列表
    init() {
      const params = {
        fileGroupId: this.curGroupInfo.id
      }
      return new Promise((resolve, reject) => {
        getListApi({
          ...params
        }).then(res => {
          const { code, data } = res
          if (!code) {
            this.tableData = data.map(c => ({ ...c, fileUrl: `${process.env.VUE_APP_BASE_API_WMS}${c.fileUrl}` }))
          }
          resolve(res)
        }).catch(err => {
          reject(err)
        })
      })
    },
    handleRowClick({ row, column, cell, event }) {
      if (column.label === '操作') {
        return false
      }
      this.rightObj = { ...row }
    },
    refresh() {
      // 更新分页
      this.init('clean')
    },
    // 下载文件  word和excel直接下载，图片和pdf直接打开   /sys/file/preview/fileId
    handleDownloadFile(item) {
      const isImg = ['png', 'jpg', 'jpeg', 'bmp', 'gif'].includes(item.suffix)
      const isTxt = ['txt'].includes(item.suffix)
      if (isImg) {
        this.$viewerApi({
          options: {
            toolbar: false,
            navbar: false,
            title: false
          },
          images: [item.fileUrl]
        })
      } else if (isTxt) {
        axios.get(item.fileUrl, { responseType: 'blob' })
          .then(res => {
            downloadFile(item.fileRealName, res.data)
          })
      } else {
        window.open(item.fileUrl)
      }
    },
    handleFileClick(file) {
      // 处理文件点击事件，例如跳转到文件下载页面或触发下载逻辑
      // 在实际应用中，你可能会调用一个 API 来下载文件或跳转到文件下载页面
    },
    handleAdd() {
      this.dialogForm = {}
      this.editType = 'add'
      this.dlgView = true
    },
    // 删除单据信息
    handleDelete(item) {
      this.$confirm('此操作将永久删除文档信息, 是否继续?', '提示', {
        confirmButtonText: '确 定',
        cancelButtonText: '取 消',
        type: 'warning',
        cancelButtonClass: 'btn-custom-cancel',
        lockScroll: false
      }).then(() => {
        delApi({
          id: item.id
        }).then(res => {
          const { code } = res
          if (!code) {
            this.$notify.success({
              title: '成功',
              message: '删除成功'
            })
            this.init()
          }
        })
      }).catch((e) => {
        this.$notify.info({
          title: '消息',
          message: '已取消删除'
        })
      })
    },
    // 编辑
    handleModify(item) {
      this.dialogForm = { ...item }
      this.editType = 'edit'
      this.dialogVisible = true
    },
    // 新增文件分组
    handleAddTag() {
      const params = {
        orderId: this.$route.params.id
      }
      return new Promise((resolve, reject) => {
        addFileGroupApi({
          ...params
        }).then(res => {
          const { code } = res
          if (!code) {
            this.getFileGroupList()
              .then(res => {
                const { data } = res
                if (data.length) {
                  this.handleClickTag(data[data.length - 1])
                  this.$notify.success({
                    title: '成功',
                    message: '新增成功，已帮您定位到最后一个分组'
                  })
                }
              })
          }
          resolve(res)
        }).catch(err => {
          reject(err)
        })
      })
    },
    // 删除文件分组
    handleDelTag(c) {
      const params = {
        id: c.id
      }
      return new Promise((resolve, reject) => {
        deleteFileGroupApi({
          ...params
        }).then(res => {
          const { code } = res
          if (!code) {
            this.getFileGroupList()
              .then(res => {
                const { data } = res
                if (data.length) {
                  this.handleClickTag(data[data.length - 1])
                  this.$notify.success({
                    title: '成功',
                    message: '删除成功，已帮您定位到最后一个分组'
                  })
                  return
                }
                // 无分组清空分组下的上传数据
                this.tableData = []
              })
          }
          resolve(res)
        }).catch(err => {
          reject(err)
        })
      })
    },
    // 获取文件分组列表
    getFileGroupList() {
      const params = {
        orderId: this.$route.params.id
      }
      return new Promise((resolve, reject) => {
        getFileGroupListApi({
          ...params
        }).then(res => {
          const { code, data } = res
          if (!code) {
            this.fileGroupList = data
            const time = Math.ceil(this.fileGroupList.length / 20)
            let arr = []
            for (let index = 0; index < time; index++) {
              arr = [...arr, ...this.colors]
            }
            this.colors = time === 0 ? [...this.colors] : [...arr]
          }
          resolve(res)
        }).catch(err => {
          reject(err)
        })
      })
    },
    // 点击文件分组
    handleClickTag(c) {
      // 防止重复调用接口
      if (this.curGroupInfo.id && this.curGroupInfo.id === c.id) return
      this.curGroupInfo = c
      this.init()
    }
  }
}
</script>

<style lang="scss" scoped>
.wrapperBox {
  width: 100%;
  display: flex;
  flex-flow: row wrap;
  justify-content: flex-start;
  .boxItem {
    position: relative;
    // flex: 0 0 auto;
    flex: 1;
    // width: 250px;
    /* 这里的125px = (分布个数6-1)*间隙25px, 可以根据实际的分布个数和间隙区调整 */
    width: calc((100% - 125px) / 6);
    min-width: calc((100% - 125px) / 6);
    max-width: calc((100% - 125px) / 6);
    height: 100px;
    box-sizing: border-box;
    padding: 10px;
    /* 间隙为25px */
    margin: 0 25px 25px 0;
    border-radius: 10px;
    display: flex;
    justify-content: space-between;
    // 删除按钮
    .closeBtn {
      position: absolute;
      right: 10px;
      top: 10px;
      opacity: 0;
      transition: opacity 0.3s ease;
      cursor: pointer;
      & > .closeIcon {
        font-size: 23px;
        color: #FF0000;
      }
    }
    &:hover .closeBtn {
      opacity: 1;
    }
  }
  .boxItem:nth-child(6n) {
    /* 去除第3n个的margin-right */
    margin-right: 0;
  }
  .checkedFileGroup {
    box-shadow: 0 0 10px 0 #000;
  }
}
.addBoxItemBtn {
  width: 100px;
  height: 100px;
  border: 1px solid #000;
  box-sizing: border-box;
  margin: 0 25px 25px 0;
  font-size: 50px;
  line-height: 100px;
  text-align: center;
  border-radius: 10px;
  box-shadow: 0 0 10px 0 #000;
  cursor: pointer;
}
.boxItemLeft {
  width: 50%;
  display: flex;
  flex-flow: column wrap;
  justify-content: space-between;
  & > div:nth-child(1) {
    color: #FFF;
    font-size: 23px;
    font-weight: bold;
  }
  & > div:nth-child(2) {
    font-weight: 700;
  }
}
.boxItemRight {
  width: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  & > .pic {
    font-size: 50px;
  }
}
.tagContent {
  padding: 15px;
  box-sizing: border-box;
  height: 650px;
  min-height: 650px;
  overflow-y: auto;
  border: 1px solid #BCBCBC;
}
::v-deep .empty-txt {
    // .el-empty__image {
    //     display: none;
    // }
}
.box-list {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    box-sizing: border-box;
}
.box-item {
    width: 48%;
    display: flex;
    justify-content: space-between;

    box-sizing: border-box;

    border: 1px solid #ccc;
    border-radius: 5px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
    cursor: pointer;

    margin-bottom: 10px;
    padding: 10px;
}
.box-item-left {
    // width: 65%;
    // display: flex;
    // flex-direction: column;
    // justify-content: space-around;
}
.box-item-right {
    width: 30%;
    display: flex;
    align-items: center;
}
</style>
