<template>
  <el-drawer
    :center="true"
    :append-to-body="true"
    :close-on-press-escape="false"
    :wrapper-closable="false"
    :title="title"
    size="50"
    :visible="value"
    :destroy-on-close="true"
    :before-close="beforeClose"
    @open="initSelect"
  >
    <el-form ref="form" :model="form" :rules="rules" label-width="120px">
      <el-scrollbar class="scroll-drawer">
        <el-form-item label="单据类型" prop="type">
          <el-select v-model="form.type" filterable clearable placeholder="请选择" style="width: 178px;">
            <el-option
              v-for="item in [{name: '交接单', id: '1'},{name: '验收单', id: '2'},{name: '入库单', id: '3'},{name: '其它', id: '4'}]"
              :key="item.id"
              :label="item.name"
              :value="item.id"
            />
          </el-select>
        </el-form-item>
        <el-form-item label="文档名称" prop="docName">
          <el-input v-model="form.docName" clearable placeholder="请输入" style="width: 178px;" />
        </el-form-item>
        <el-form-item label="附件" prop="fileId">
          <el-upload
            ref="upload"
            sizi="mini"
            :action="`${BASE_API}/wms/file/upload`"
            :headers="headers"
            accept=".xls,.xlsx"
            :file-list="fileList"
            :on-success="handleUploadSuccess"
            :on-remove="handleRemove"
            :before-remove="beforeRemove"
          >
            <el-button size="mini" icon="el-icon-upload" type="primary">点击上传</el-button>
          </el-upload>
        </el-form-item>
      </el-scrollbar>
      <div class="drawer-footer">
        <el-button :loading="loading" type="primary" @click="handleConfirm('form')">{{ loading ? '提交中 ...' : '确 定' }}</el-button>
        <el-button @click="beforeClose">取 消</el-button>
      </div>
    </el-form>

  </el-drawer>
</template>

<script>
import $store from '@/store'
import { getUploadedFileListCommonApi } from '@/apis/modules/common'
import { addApi, editApi } from '@/apis/modules/in-storage/purchase/document'

const INIT_FORM = () => ({
  orderId: '', // 订单id
  type: '', // 单据类型
  docName: '', // 文档名称
  fileId: '', // 值为附件列表id拼接的字符串
  fileName: ''
})

export default {
  components: {
  },
  mixins: [],
  props: {
    value: {
      type: Boolean,
      default: false
    },
    title: {
      type: String,
      default: ''
    },
    editType: {
      type: String,
      default: 'add'
    },
    dialogForm: {
      type: Object,
      default: () => {
        return { ...INIT_FORM() }
      }
    },
    extraInfo: {
      type: Object,
      default: () => ({})
    }
  },
  data() {
    const token = `${$store.getters.token}`
    return {
      form: INIT_FORM(),
      loading: false,
      rules: {
        type: [
          { required: true, message: '请选择单据类型', trigger: 'change' }
        ],
        fileId: [
          { required: true, message: '请上传文件', trigger: 'change' }
        ]
      },
      headers: { 'Authorization': token },
      fileList: [],
      BASE_API: process.env.VUE_APP_BASE_API_WMS
    }
  },
  watch: {
    dialogForm: {
      handler: function(value) {
        if (this.editType === 'add') {
          this.form = { ...INIT_FORM() }
        } else {
          this.form = { ...value }
          this.form.fileId && this.initFileList()
        }
      },
      immediate: true
    },
    fileList: {
      handler(list) {
        if (!list.length) {
          this.form.fileId = ''
          this.form.fileName = ''
        } else {
          this.form.fileId = list[0].id
          this.form.fileName = list[0].fileName
        }
        this.$refs.form && this.$refs.form.validateField('fileId')
      },
      immediate: true
    }
  },
  methods: {
    initSelect() {
    },
    handleConfirm(formName) {
      this.$refs[formName].validate((valid) => {
        const api = this.editType === 'add' ? addApi : editApi
        if (valid) {
          this.loading = false
          api({ ...this.form, docName: this.form.docName || this.form.fileName, ...this.extraInfo }).then(res => {
            const { code } = res
            if (!code) {
              this.$message({
                message: `${this.title}成功`,
                type: 'success'
              })
              this.$emit('submit')
              this.beforeClose()
            }
            this.loading = false
          }).catch(err => {
            console.log(err)
            this.loading = false
          })
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
    // 文件上传成功
    handleUploadSuccess(response, file, fileList) {
      const { code, data, msg } = response
      if (code && code === 999) {
        this.$notify.error({
          title: '错误',
          message: msg || '文件上传失败'
        })
        return
      }
      if (!code) {
        this.fileList = [{ ...data, fileId: data.id, name: data.fileName, url: data.fileUrl }]
      }
    },
    // 文件移除前
    beforeRemove(file, fileList) {
      return this.$confirm(`确定移除 ${file.name}？`, {
        cancelButtonClass: 'cancel-button-class',
        confirmButtonClass: 'confirm-button-class'
      })
    },
    // 文件移除操作
    handleRemove(file, fileList) {
      this.fileList = []
    },
    // 反选的时候获取默认的文件列表
    initFileList() {
      return new Promise((resolve, reject) => {
        getUploadedFileListCommonApi({
          ids: [this.form.fileId]
        }).then(res => {
          const { code, data } = res
          if (!code) {
            this.fileList = data.length ? [{ ...data[0], fileId: data[0].id, name: data[0].fileName, url: data[0].fileUrl }] : []
          }
          resolve(res)
        }).catch(err => {
          reject(err)
        })
      })
    },
    beforeClose(done) {
      this.$refs.form.resetFields()
      this.form = INIT_FORM()
      this.fileList = []
      this.$emit('input', false)
      typeof done === 'function' && done()
    }
  }
}
</script>

<style lang='scss' scoped>
.container{
width: 860px;
margin: 0 auto;
.left{
    width: 640px;
}
.right{
    margin-left: 20px;
    width: 180px;
}
}
::v-deep:focus {
outline: 0;
}
.scroll-drawer {
height: calc(100vh - 150px);
// overflow: auto;
}
::v-deep .el-scrollbar .el-scrollbar__wrap {
overflow-x: hidden;
}
::v-deep .el-drawer {
width: 640px;
}
.drawer-footer{
text-align: right;
}

::v-deep .el-upload-list__item.is-success.focusing .el-icon-close-tip {
display: none !important;
}

</style>

